.social {
  display: flex;
  position: relative;
  width: 50%;
  margin-right: 100px;
  flex-direction: row-reverse;
  align-items: flex-end;
}

.social ul {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social ul li {
  position: relative;
  list-style: none;
  transition: transform 0.2s;
}

.social ul li a {
  position: relative;
  margin: 0 5px;
  letter-spacing: 1px;
  font-weight: 500px;
  transition: transform 0.2s;
}

.social ul li:hover {
  transform: scale(1.5);
}

